import React, { useEffect, useState } from 'react'
import { Table, Pagination, Row, Col, OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import { FaRegEye, FaRegComments } from 'react-icons/fa'
import { Link, useHistory} from 'react-router-dom';
import { IconContext } from 'react-icons';
import { useDispatch } from 'react-redux';
import { setOrder } from 'student/store/order';
import moment from 'moment';

import styles from './Table.module.css';


const TableBoot = ({ data = [], paginationHandler = () => { }, total = 10 }) => {
    const dispatch = useDispatch();
    const history = useHistory();
    const [pagData, setPagData] = useState({
        page: 0, row: 5
    });
    const pagNumber = [...Array(
        Math.ceil(+total / (+pagData.row)) ?? 10
    ).keys()] || []
    const pagHandler = (a) => {
        if (a === 'next') {
            if (pagData.page !== (pagNumber.length - 1)) setPagData({ ...pagData, page: +pagData.page + 1 });
        } else {
            if (pagData.page >= 1) setPagData({ ...pagData, page: +pagData.page - 1 });
        }
    }
    useEffect(() => {
        paginationHandler(pagData);
    }, [pagData])
    
    const viewClickHandler = (v)=>{
        dispatch(setOrder(v));
        history.push('/dashbord/inner-order');
        
    } 
    const messageClickHandler = (v)=>{
        dispatch(setOrder(v));
        history.push("/dashbord/chat-support");
    }

    return (
        <div className='bg-white rounded overflow-scroll'>
            <IconContext.Provider value={{ color: "#000", size: 20, paddingLeft: 10 }}>
                <Table striped className={styles.table} >
                    <thead className={styles.tableInfo} >
                        <tr className={styles.tableInfo}>
                            <th>Order Id</th>
                            <th>Country</th>
                            <th>Subject</th>
                            <th>Deadline</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody className={`${styles.tableInfo} ${styles.table} ${styles.tableBody} `} >
                        {data.length ? data.map(e => {
                            return (
                                <tr key={e._id} className={styles.tableInfo}>
                                    <td>{e._id}</td>
                                    <td>{e.country}</td>
                                    <td>{e.subject}</td>
                                    <td>{moment(e.deadline).format('DD/MM/YYYY')}</td>
                                    <td>
                                        <OverlayTrigger
                                            placement='top'
                                            overlay={
                                                <Tooltip id={`tooltip-top`}>
                                                    view
                                                </Tooltip>
                                            }
                                        >
                                            <Button variant="link" onClick={()=>viewClickHandler(e)}>
                                                <FaRegEye />
                                            </Button>
                                        </OverlayTrigger>
                                        <OverlayTrigger
                                            placement='top'
                                            overlay={
                                                <Tooltip id={`tooltip-top`}>
                                                    messages
                                                </Tooltip>
                                            }
                                        >
                                            <Button variant="link" onClick={()=>messageClickHandler(e)}>
                                            <FaRegComments />
                                            </Button>
                                        </OverlayTrigger>

                                        <Link to="#">
                                        </Link>
                                    </td>
                                </tr>
                            )
                        }) : <tr className={styles.tableInfo}>
                            <td colSpan="5" className='text-center bg-white'>There is no Any Order </td>
                        </tr>
                        }
                    </tbody>
                </Table>
            </IconContext.Provider>
            <Row className='p-1 m-2'>
                <Col md={6} className='text-style'>
                    <p >Showing {(+pagData.page) * (+pagData.row)} to {(+pagData.page + 1) * (pagData.row)} of {total} entries</p>
                </Col>
                <Col md={6}>
                    <Pagination>

                        <Pagination.Item onClick={() => pagHandler('pre')} >{'Privious'}</Pagination.Item>
                        {pagNumber.map((e) => {
                            return (
                                <Pagination.Item onClick={() => setPagData({ ...pagData, page: +e })} active={pagData.page === +e} key={e}>{(+e) + 1}</Pagination.Item>
                            )
                        })}
                        <Pagination.Item onClick={() => pagHandler('next')} >{'Next'}</Pagination.Item>
                    </Pagination>
                </Col>

            </Row>
        </div>

    )
}

export default TableBoot;
