import React from 'react'
import { Switch, Route, Redirect } from "react-router-dom";


import styles from "student/App.module.css";
import Navbar from "student/components/Navbar/Navbar";
import Layout from "student/pages/dashboard";
import Login from 'student/admin/pages/login/Login'
import { useSelector } from "react-redux";



const Admin = () => {
    return (
      <Switch>
        <Route path="/admin/login">
          <Login isAdmin={true} />
        </Route>
        <PrivateAdminRouet path="/admin/dashboard">
          <div className={styles.main}>
            <Navbar />
            <Layout />
          </div>
        </PrivateAdminRouet>
        <Route path="/admin">
          <Redirect to="/admin/dashboard" />
        </Route>
      </Switch>
    );
}

const PrivateAdminRouet = ({ children, ...rest }) => {
  const { isAdminAuth } = useSelector((state) => state.auth);
  return (
    <Route
      {...rest}
      render={({ location }) => {
        return isAdminAuth ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/admin/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
};

export default Admin
